import * as React from 'react';
import { graphql, useStaticQuery } from 'gatsby';
import { GatsbyImage, getImage } from 'gatsby-plugin-image';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import Link from '@mui/material/Link';
import Typography from '@mui/material/Typography';
import Section from '../section';
import SectionTitle from '../section-title';
import InstagramIcon from '../../images/icon/trainer_instagram.svg';
// import TwitterIcon from '../../images/icon/trainer_twitter.svg';
// import TiktokIcon from '../../images/icon/trainer_tiktok.svg';

const SectionTrainer = () => {
  const data = useStaticQuery(graphql`
    query {
      calvin: file(relativePath: { eq: "640x640-trainer-calvin.jpg" }) {
        childImageSharp {
          gatsbyImageData
        }
      }
      kashima: file(relativePath: { eq: "640x640-trainer-kashima.jpg" }) {
        childImageSharp {
          gatsbyImageData
        }
      }
      kohinata: file(relativePath: { eq: "640x640-trainer-kohinata.jpg" }) {
        childImageSharp {
          gatsbyImageData
        }
      }
    }
  `);

  return (
    <Section id='trainer' theme='light'>
      <SectionTitle theme='light'>Trainer</SectionTitle>
      <Grid container spacing={4}>
        {[
          {
            image: data.calvin,
            name: 'ジョー',
            en_name: 'Joe',
            description:
              '自重トレーニングで理想の体づくりを得意としております。カナダと日本のハーフで日本語・英語・フランス語対応可能です。栄養学も学んでおり、食事面のサポートも可能です。健康的で効率的なトレーニングをしましょう！',
            sns: [
              {
                image: InstagramIcon,
                link: 'https://www.instagram.com/workout828official/'
              }
            ]
          },
          {
            image: data.kashima,
            name: '鹿島 義慈',
            en_name: 'Yoshinari Kashima',
            description:
              '鍼灸指圧師の資格を持っており、身体に関する知識から、正しいフォーム指導や、効率よく筋肉を付けるトレーニングメソッド・食事、ダイエットまで幅広く対応可能です。これまで男女年齢問わず、多くの指導実績有り。楽しく続けて理想の身体を手に入れましょう。',
            sns: []
          },
          {
            image: data.kohinata,
            name: '小日向 大輔',
            en_name: 'Daisuke Kohinata',
            description:
              '“用意が出来てないからやらないんじゃない。今の行動で全てが変わります。遠回りでも、牛の歩みでも、確実に、着実に。後悔よりも航海です” そんな道のりを一緒に歩むお手伝いをさせて下さい。いつになっても挑戦出来ます。フィットネストレーナー兼現役モデルが全力で応援致します。',
            sns: [
              {
                image: InstagramIcon,
                link: 'https://www.instagram.com/addisium/'
              }
            ]
          }
        ].map(({ image, name, en_name, description, sns }, i) => {
          return (
            <Grid item key={i} md={6}>
              <Grid container spacing={3}>
                <Grid item xs={12} sm={6}>
                  <GatsbyImage image={getImage(image)} alt={en_name} />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <Typography
                    sx={{
                      fontSize: '24px',
                      lineHeight: '26px'
                    }}
                  >
                    {name}
                  </Typography>
                  <Typography
                    sx={{
                      fontSize: '12px',
                      lineHeight: '26px',
                      letterSpacing: '0.6px',
                      color: '#b28223'
                    }}
                  >
                    {en_name}
                  </Typography>
                  <Typography
                    sx={{
                      marginTop: '1em',
                      lineHeight: '26px'
                    }}
                  >
                    {description}
                  </Typography>
                  <Box
                    sx={{
                      mt: 1
                    }}
                  >
                    {sns.map(({ image, link }, i) => (
                      <Link
                        key={i}
                        href={link}
                        target='_blank'
                        sx={{
                          mr: 1
                        }}
                      >
                        <img src={image} alt={name} width={36} height={36} />
                      </Link>
                    ))}
                  </Box>
                </Grid>
              </Grid>
            </Grid>
          );
        })}
      </Grid>
      <Typography variant='body2' align='center' mt={10}>
        ※ Trainer に掲載希望の方は{' '}
        <Link
          href='https://works.do/R/ti/p/addictgymjp@addictgym'
          target='_blank'
          rel='noopener noreferrer'
          sx={{
            '&:hover': {
              color: '#2e2e2e'
            }
          }}
        >
          公式 LINE
        </Link>{' '}
        よりご連絡ください。
      </Typography>
    </Section>
  );
};

export default SectionTrainer;
